@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  min-height: 100vh;
  background-color: rgb(240, 240, 240);
  font-family: 'Poiret One', cursive;
  color: rgb(60, 60, 60);
  letter-spacing: 0.8px;
}

.header {
  font-size: 26px;
  padding: 10px 20px;
  letter-spacing: 0;
}

.reference {
  text-decoration: none;
  color: rgb(220, 172, 255);
  font-weight: bold;
  transition: ease-in-out 0.2s;
}

.reference:hover {
  color: rgb(205, 140, 250);
}

.menu {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 400px;
}

.button {
  width: 180px;
  height: 60px;
  margin: 0 30px;
  font-family: 'Poiret One', cursive;
  font-size: 20px;
  font-weight: bold;
  color: rgb(60, 60, 60);
  border-radius: 10px;
  border-style: none;
  box-shadow: 5px 5px 10px rgb(210, 215, 220), -5px -5px 15px white;
  transition: ease-in-out 0.2s;
}

#startTestButton:hover,
#addAQuestionButton:hover
{
  background-color: rgb(220, 172, 255);
  color: white;
  cursor: pointer;
}

#startTestButton:active,
#addAQuestionButton:active 
{
  background-color: rgb(205, 140, 250);
  color: white;
  box-shadow: none;
}
.label {
  font-size: 18px;
  word-wrap: inherit;
  width: 450px;
}

.error {
  font-size: 16px;
  color: rgb(230, 80, 80);
  padding-top: 4px;
}

.textInputWrapper {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0;
  padding-bottom: 20px;
}

.textInput {
  box-sizing: border-box;
  height: 26px;
  width: 450px;
  margin-top: 8px;
  border-style: none;
  border-radius: 5px;
  transition: 0.1s;
}

.textInput:focus,
.textInput:hover {
  outline: 0;
  border-bottom: 1px solid rgb(220, 172, 255);
}

.textInputError,
.textInputError:focus,
.textInputError:hover {
  border-bottom: 1px solid rgb(230, 180, 180);
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 15vh;
}

.questionCreatorMenu {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.questionCreatorButton {
  font-family: 'Poiret One', cursive;
  font-size: 17px;
  font-weight: bold;
  margin: 20px 20px;
  height: 40px;
  width: 120px;
  border-radius: 5px;
  border-style: none;
  box-shadow: 5px 5px 15px rgb(210, 215, 220), -5px -5px 15px white;
  transition: ease-in-out 0.2s;
}

#cancelButton {
  color: rgb(230, 80, 80);
}

#cancelButton:hover {
  background-color: rgb(230, 80, 80);
  color: white;
}

#cancelButton:active {
  background-color: rgb(210, 70, 70);
  color: white;
  box-shadow: none;
}

#okButton {
  color: rgb(60, 155, 30);
}

#okButton:hover {
  background-color: rgb(115, 190, 94);
  color: white;
}

#okButton:active {
  background-color: rgb(90, 150, 75);
  color: white;
  box-shadow: none;
}

.test {
  color: rgb(60, 60, 60);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0;
  font-size: 18px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  word-wrap: break-word;
}

li.question::marker {
  font-size: 26px;
  color: rgb(205, 140, 250);
}

.question {
  width: 600px;
  word-wrap: inherit;
}

.optionList {
  padding: 4px 0;
}

.option {
  list-style: none;
  font-size: 16px;
  word-wrap: inherit;
  padding: 4px 0;
}

.optionText {
  padding-left: 8px;
}

#submitResultButton {
  font-family: 'Poiret One', cursive;
  font-size: 18px;
  font-weight: bold;
  color: rgb(60, 60, 60);
  height: 45px;
  width: 135px;
  border-radius: 5px;
  border-style: none;
  box-shadow: 5px 5px 10px rgb(190, 200, 210), -5px -5px 15px white;
  margin-bottom: 35px;
  transition: ease-in-out 0.2s;
}

#submitResultButton:hover {
  background-color: rgb(220, 172, 255);
  color: white;
  cursor: pointer;
}

#submitResultButton:active {
  background-color: rgb(205, 140, 250);
  color: white;
  box-shadow: none;
}

.checkbox:hover {
  cursor: pointer;
}

